@import url("https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap");
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body, button, input {
  font-family: "Space Mono", sans-serif;
  font-weight: 700;
}

body {
  background-color: hsl(185deg, 41%, 84%);
  font-size: 24px;
}
@media (min-width: 1100px) {
  body {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

header {
  height: 6em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 1100px) {
  header {
    justify-content: flex-start;
  }
}

h1 {
  font-size: 1.15em;
  letter-spacing: 0.4em;
  width: 4.2em;
  word-break: break-word;
  color: hsl(183deg, 100%, 15%);
  opacity: 75%;
  text-align: center;
}

h2 {
  color: hsl(0deg, 0%, 100%);
  font-size: 0.7em;
  margin-top: 1.2em;
}

.h3 {
  color: hsl(186deg, 14%, 43%);
  font-size: 0.6em;
  margin-bottom: 0.5em;
}

.btn {
  text-transform: uppercase;
  border: none;
  border-radius: 0.3em;
  background-color: hsl(172deg, 67%, 45%);
  padding: 0.45em 0;
  margin-top: 1em;
  color: hsl(183deg, 100%, 15%);
  font-size: 0.9em;
  cursor: pointer;
  transition: transform 0.15s, background-color 0.2s, color 0.2s;
}
.btn:active {
  transform: scale(0.9);
}
.btn--tip {
  background-color: hsl(183deg, 100%, 15%);
  color: hsl(0deg, 0%, 100%);
  padding: 0.3em 0;
  margin: 0.3em 0;
}
.btn--tip--active {
  background-color: hsl(185deg, 41%, 84%);
  color: hsl(183deg, 100%, 15%);
}
.btn:hover {
  background-color: hsl(185deg, 41%, 84%);
  color: hsl(183deg, 100%, 15%);
}

.input {
  border: none;
  padding: 0.2em 0.6em 0.2em 2em;
  text-align: right;
  font-size: 1em;
  color: hsl(183deg, 100%, 15%);
  background-color: hsl(189deg, 41%, 97%);
  border-radius: 0.1em;
  width: 100%;
  caret-color: hsl(172deg, 67%, 45%);
  cursor: pointer;
}
.input:focus {
  outline: 2px hsl(172deg, 67%, 45%) solid;
}
.input.input--error {
  outline: 2px hsl(20deg, 74%, 68%) solid;
}
.input::placeholder {
  color: hsl(184deg, 14%, 56%);
}
.input--full {
  padding-left: 0;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

.calculator {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: hsl(0deg, 0%, 100%);
  border-radius: 1em;
  padding: 1.2em 1.1em;
}
@media (min-width: 1100px) {
  .calculator {
    flex-direction: row;
    justify-content: space-between;
    margin: auto;
    width: 40em;
  }
}
.calculator__inputs {
  margin: 0.4em;
}
.calculator__inputs label[for=bill] {
  position: relative;
}
.calculator__inputs label[for=bill]:after {
  content: url("../static/icon-dollar.svg");
  font-weight: 400;
  font-size: 1.8em;
  display: block;
  position: absolute;
  top: 1.2em;
  left: 0.7em;
}
.calculator__inputs label[for=people] {
  position: relative;
}
.calculator__inputs label[for=people]:after {
  content: url("../static/icon-person.svg");
  font-weight: 400;
  font-size: 1.8em;
  display: block;
  position: absolute;
  top: 1.1em;
  left: 0.7em;
}
.calculator__inputs .input-wrapper {
  display: flex;
  flex-direction: column;
  padding-bottom: 1em;
  position: relative;
}
.calculator__inputs .input-wrapper .error-text {
  display: none;
  position: absolute;
  font-size: 0.6em;
  width: 100%;
  text-align: right;
  color: hsl(20deg, 74%, 68%);
}
.calculator__inputs .input-wrapper .error-text--display {
  display: block;
}
.calculator__inputs .tips-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.calculator__inputs .tips-wrapper button, .calculator__inputs .tips-wrapper input[type=number] {
  width: 47.5%;
}
@media (min-width: 700px) {
  .calculator__inputs .tips-wrapper button, .calculator__inputs .tips-wrapper input[type=number] {
    width: 31.5%;
  }
}
.calculator__results {
  display: flex;
  flex-direction: column;
  background-color: hsl(183deg, 100%, 15%);
  border-radius: 0.7em;
  padding: 1em;
}
@media (min-width: 1100px) {
  .calculator__results {
    flex-basis: 55%;
    margin-left: 1.6em;
    padding: 1.8em;
  }
}
.calculator__results__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.calculator__results__wrapper__amount {
  color: hsl(172deg, 67%, 45%);
  font-size: 1.5em;
}
@media (min-width: 1100px) {
  .calculator__results__reset {
    margin-top: auto;
  }
}

