@import url('https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap');

$c-primary: hsl(172, 67%, 45%);
$c-secondary: hsl(183, 100%, 15%);
$c-title: hsl(186, 14%, 43%);
$c-placeholder: hsl(184, 14%, 56%);
$c-main-bkd: hsl(185, 41%, 84%);
$c-input-bkd: hsl(189, 41%, 97%);
$c-white: hsl(0, 0%, 100%);
$c-error: hsl(20, 74%, 68%);
$font: 24px;

@mixin break-md {
  @media (min-width: 700px) {
    @content
  }
}

@mixin break-lg {
  @media (min-width: 1100px) {
    @content
  }
}