.calculator {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: $c-white;
  border-radius: 1em;
  padding: 1.2em 1.1em;

  @include break-lg {
    flex-direction: row;
    justify-content: space-between;
    margin: auto;
    width: 40em;
  }

  &__inputs {
    margin: 0.4em;

    & label[for=bill] {
      position: relative;

      &:after {
        content: url("../static/icon-dollar.svg");
        font-weight: 400;
        font-size: 1.8em;
        display: block;
        position: absolute;
        top: 1.2em;
        left: 0.7em;
      }
    }

    & label[for=people] {
      position: relative;

      &:after {
        content: url("../static/icon-person.svg");
        font-weight: 400;
        font-size: 1.8em;
        display: block;
        position: absolute;
        top: 1.1em;
        left: 0.7em;
      }
    }


    & .input-wrapper {
      display: flex;
      flex-direction: column;
      padding-bottom: 1em;
      position: relative;

      & .error-text {
        display: none;
        position: absolute;
        font-size: 0.6em;
        width: 100%;
        text-align: right;
        color: $c-error;

        &--display {
          display: block;
        }
      }
    }

    & .tips-wrapper {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;

      & button, & input[type="number"] {
        width: 47.5%;
        @include break-md {
          width: 31.5%;
        }
      }
    }
  }

  &__results {
    display: flex;
    flex-direction: column;
    background-color: $c-secondary;
    border-radius: 0.7em;
    padding: 1em;

    @include break-lg {
      flex-basis: 55%;
      margin-left: 1.6em;
      padding: 1.8em;
    }

    &__wrapper {
      display: flex;
      justify-content: space-between;
      align-items: center;

      &__amount {
        color: $c-primary;
        font-size: 1.5em;
      }
    }

    &__reset {
      @include break-lg {
        margin-top: auto;
      }
    }
  }
}