
body, button, input {
  font-family: "Space Mono", sans-serif;
  font-weight: 700;
}

body {
  background-color: $c-main-bkd;
  font-size: $font;

  @include break-lg {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

header {
  height: 6em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @include break-lg {
    justify-content: flex-start;
  }
}

h1 {
  font-size: 1.15em;
  letter-spacing: .4em;
  width: 4.2em;
  word-break: break-word;
  color: $c-secondary;
  opacity: 75%;
  text-align: center;
}

h2 {
  color: $c-white;
  font-size: 0.7em;
  margin-top: 1.2em;
}

.h3 {
  color: $c-title;
  font-size: 0.6em;
  margin-bottom: 0.5em;
}

.btn {
  text-transform: uppercase;
  border: none;
  border-radius: .3em;
  background-color: $c-primary;
  padding: .45em 0;
  margin-top: 1em;
  color: $c-secondary;
  font-size: 0.9em;
  cursor: pointer;
  transition: transform 0.15s, background-color 0.2s, color 0.2s;

  &:active {
    transform: scale(0.9);
  }

  &--tip {
    background-color: $c-secondary;
    color: $c-white;
    padding: 0.3em 0;
    margin: .3em 0;

    &--active {
      background-color: $c-main-bkd;
      color: $c-secondary;
    }
  }

  &:hover {
    background-color: $c-main-bkd;
    color: $c-secondary;
  }
}

.input {
  border: none;
  padding: .2em .6em .2em 2em;
  text-align: right;
  font-size: 1em;
  color: $c-secondary;
  background-color: $c-input-bkd;
  border-radius: 0.1em;
  width: 100%;
  caret-color: $c-primary;
  cursor: pointer;

  &:focus {
    outline: 2px $c-primary solid;
  }

  &.input--error {
    outline: 2px $c-error solid;
  }

  &::placeholder {
    color: $c-placeholder;
  }

  &--full {
    padding-left: 0;
  }

}


// Remove arrow in input[type=number]
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}